import { observable } from 'mobx'

const orderStore = observable({
  b2bGlobalQty: [],
  b2bGlobalSpecIds: [],
  b2cGlobalQty: [],
  b2cGlobalSpecIds: [],
  b2bRegionQty: [],
  b2bRegionSpecIds: [],
  b2cRegionQty: [],
  b2cRegionSpecIds: [],
  mallType: 0,
  buyImmediately: 0,
  orderIds: [],

  setB2bGlobalQty(b2bGlobalQty) {
    this.b2bGlobalQty = b2bGlobalQty
  },

  setB2bGlobalSpecIds(b2bGlobalSpecIds) {
    this.b2bGlobalSpecIds = b2bGlobalSpecIds
  },

  setB2cGlobalQty(b2cGlobalQty) {
    this.b2cGlobalQty = b2cGlobalQty
  },

  setB2cGlobalSpecIds(b2cGlobalSpecIds) {
    this.b2cGlobalSpecIds = b2cGlobalSpecIds
  },

  setB2bRegionQty(b2bRegionQty) {
    this.b2bRegionQty = b2bRegionQty
  },

  setB2bRegionSpecIds(b2bRegionSpecIds) {
    this.b2bRegionSpecIds = b2bRegionSpecIds
  },

  setB2cRegionQty(b2cRegionQty) {
    this.b2cRegionQty = b2cRegionQty
  },

  setB2cRegionSpecIds(b2cRegionSpecIds) {
    this.b2cRegionSpecIds = b2cRegionSpecIds
  },

  setMallType(mallType) {
    this.mallType = mallType
  },

  setBuyImmediately(buyImmediately) {
    this.buyImmediately = buyImmediately
  },

  setOrderIds(orderIds) {
    this.orderIds = orderIds
  },

  getOrderIds() {
    return this.orderIds;
  }
})

export default orderStore